/* General styles */
body {
    font-family: Arial, sans-serif;
    background-color: #f7f7f7;
    margin: 0;
    padding: 0;
}

#transactionHistoryWrapper {
    width: 100%;
    padding: 20px;
    background-color: #51087e;
}

#searchBarWrapper {
    margin-bottom: 20px;
}

#filterWrapper {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#filterWrapper h4 {
    margin: 0;
    font-size: 18px;
}

#searchBar {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

#transactionListWrapper {
    background-color: #fff;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.transactionItem {
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    border-bottom: 1px solid #eee;
}

.transactionItem:last-child {
    border-bottom: none;
}

.transactionItem p {
    margin: 0;
    font-size: 16px;
}

.statusSuccess {
    color: green;
}

.statusFailed {
    color: red;
}

#filterSelect {
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.transactionHistoryWrapper {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #91008645;
}

.searchBarWrapper {
    margin-top: 10px;
}

.transactionListWrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 0.5cm;
    width: 85%;
    height: fit-content;
    text-align: center;
    background-color: #ffffff;
    padding: 0.5cm;
    border-radius: 0.4cm;
}


.filterWrapper {
    display: flex;
    flex-direction: row;
    gap: 0.3cm;
    align-items: center;
}

