/* Style for individual account items */
.account-item {
    display: flex;
    flex-direction: row;
    gap: 0.5cm;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    padding: 0.3cm 1cm;
    border-radius: 0.4cm;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Optional: Hover effect for account items */
.account-item:hover {
    cursor: pointer;
    transform: translateY(-5px); /* Slight lift effect */
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.1); /* Enhanced shadow on hover */
}

/* Account item details (e.g., name, balance) */
.account-item .account-name {
    font-size: small;
    font-weight: bold;
    color: #2c2b2b;
}

.account-item .account-balance {
    font-size: 1.1rem;
    color: #007bff; /* Blue for financial amounts */
    font-weight: 500;
}

/* Account icons or images */
.account-item img {
    width: 2.5cm; /* Set size for account logos or icons */
    height: auto;
    border-radius: 0.2cm; /* Slightly rounded images */
}


