.card {
    width: 85%;
    height: 5cm;
    background: linear-gradient(135deg, #ff5f6d, #ffc371); /* Pink to light orange gradient */
    border-radius: 0.6cm; /* Slightly larger radius for a softer look */
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2), 0px 6px 10px rgba(0, 0, 0, 0.1); /* Material shadow */
    align-self: center;
    justify-self: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0.5cm;
    position: relative;
    overflow: hidden;
    color: white; /* Ensure the text is visible */
    font-family: 'Roboto', sans-serif; /* Material Design-inspired font */
    transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth hover effect */
}

/* Add hover effect */
.card:hover {
    cursor: pointer;
    transform: translateY(-10px); /* Lift effect */
    box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.3), 0px 8px 15px rgba(0, 0, 0, 0.1); /* Enhanced shadow */
}

/* Optional: Card inner content style */
.card-content {
    text-align: center;
    padding: 1cm;
}

.card-title {
    font-size: 1.2cm;
    font-weight: bold;
    margin-bottom: 0.3cm;
}

.card-subtitle {
    font-size: 0.8cm;
    opacity: 0.8;
}


