/*scroll bar --webkit*/
/* Scrollbar styling for WebKit-based browsers */
::-webkit-scrollbar {
    width: 10px;
    height: 4px; /* Height for horizontal scrollbar */

  }
  
  ::-webkit-scrollbar-track {
    background: linear-gradient(45deg, #ff6b6b, #feca57, #54a0ff, #1dd1a1); /* Fancy gradient background */
    border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: black;
    border-radius: 10px;
    border: 2px solid transparent; /* Adds padding between scrollbar and track */
    background-clip: content-box; /* Ensures gradient fills scrollbar */
    
  }
  
  ::-webkit-scrollbar-thumb:hover {
    cursor: pointer;
    opacity: 0.8;
  }
  