.navbarWrapper {
  display: flex;
  justify-content: space-around;
  /* background-color: #ffcaa338; */
  height: fit-content;
  width: 100%;
  height: 1cm;
  position: fixed;
  bottom: 0;
  z-index: 1;
  padding: 0.2cm;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0 -4px 8px rgba(0, 0, 0, 0.2);
  background: linear-gradient(90deg, rgb(255 255 255), rgb(255 255 255 / 72%));
  border-top-right-radius: 1cm;
  border-top-left-radius: 1cm;
  z-index: 10;
  }
  
  .navbarHomeHolder,
  .navbarActivityHolder,
  .navbarPaymentHolder,
  .navbarProfileHolder {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: transparent;
  
  }
  
  .homeImg,
  .activityImg,
  .walletImg,
  .navbarProfileImg {
    width: 0.7cm;
    height: auto;
    margin-bottom: 5px;
    cursor: pointer;
  }
 